import React from "react";
import * as S from "./index.styled";

interface MeteorShowerProps {
  numMeteors: number;
}

const MeteorShower: React.FC<MeteorShowerProps> = ({
  numMeteors,
}: MeteorShowerProps) => {
  const meteors = new Array(numMeteors).fill(true);
  const meteorColors = [
    "#ff0000", // 
    "#00ff00", // 
    "#0000ff", // 
    "#ff00ff", // 
    "#ffff00", // 
    "#ff9900", // 
    "#ffcc00", // 
    "#9966ff", // 
    "#ff66cc", // 
    "#66ccff", // 
    "#cc66ff", // 
    "#66ffcc", // 
    "#33cc33", //  
    "#ff3333", //  
    "#cc99cc", // 
    "#0099cc", //  
    "#996699", //  
    "#ff9999", // 
    "#ff6666", //  
    "#666699", // 
  ];

  return (
    <S.MeteorsContainer>
      {meteors.map((el, idx) => (
        <S.Meteor
          key={"meteor" + idx}
          $top={Math.floor(Math.random() * 400) - 200 + "vh"}
          $time={Math.floor(Math.random() * 100) + "s"}
          $color={meteorColors[Math.floor(Math.random() * meteorColors.length)]}
        />
      ))}
    </S.MeteorsContainer>
  );
};

export default MeteorShower;
